.search-form-container {
  z-index: 100;
  width: 100%;
  background-color: #23449f;
  padding: 10px 20px;
  position: fixed;
  box-shadow: 0 5px 2px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.search-form {
  max-width: 420px;
  width: 100%;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  transition: outline .25s linear;
  display: flex;
  overflow: hidden;
}

.search-form:focus-within {
  outline: 3px solid #2196f3;
}

.search-form input {
  height: 40px;
  width: 100%;
  border: none;
  outline: none;
  padding: 10px;
  display: block;
}

.search-form button {
  min-width: 40px;
  color: #302d2d;
  cursor: pointer;
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  transition: color .25s linear, background-color .25s linear;
  display: flex;
}

.search-form button:hover, .search-form button:focus {
  color: #fff;
  background-color: #2196f3;
}

.icon-btn {
  fill: currentColor;
}

.gallery {
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
  padding: 80px 15px 20px;
  display: grid;
}

@media screen and (min-width: 600px) {
  .gallery {
    height: 300px;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1048px) {
  .gallery {
